export const ErrorMessage = ({ error, inputName, minLenghtValue }) => {
  if (error) {
    switch (error.type) {
      case "required":
        return <p>{inputName} is required</p>;
      case "minLength":
        return (
          <p>
            Your {inputName} has to be atleast {minLenghtValue} symbols long
          </p>
        );
      case "maxLength":
        return (
          <p>
            Your {inputName} has to be less than {minLenghtValue} symbols
          </p>
        );
      case "pattern":
        return <p>yeah</p>;

      default:
        return null;
    }
  }

  return null;
};
